import React from 'react';
import PropTypes from 'prop-types';
import Sound from 'react-sound';
import { connect } from 'react-redux';

import colors from '../../libs/styleguide/colors';
import t from '../../libs/i18n-util';

@connect(({ baccarat, modal }) => ({ baccarat, modal }))
class GameCounter extends React.Component {

  updateCountdown = () => {
    let gameConfig = selectors.getBaccaratGameConfig(this.props);
    let timeEnd = _.get(gameConfig, 'time_end');
    if (timeEnd) {
      timeEnd = moment(timeEnd).utcOffset(0, true); // set the endTime to UTC+0
      const diff = timeEnd.diff(moment());
      const countdown = moment.utc(Math.max(0, diff)).format('mm:ss');
      this.setState({ countdown });
    }
  }

  isPlaying() {
    const { countdown } = this.props;
    switch(countdown) {
    case '00:06':
    case '00:05':
    case '00:04':
    case '00:03':
    case '00:02':
    case '00:01':
      return Sound.status.PLAYING;
    default:
      return Sound.status.STOPPED;
    }
  }

  componentDidMount() {
    setInterval(this.updateCountdown, 500);
  }

  render() {
    const styles = {
      root: {
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'center',
        alignItems: 'center',
        height: 68,
        textAlign: 'center',
        borderStyle: 'solid',
        borderColor: '#979797',
        borderRadius: 2,
        borderWidth: 1,
      },
      time: {
        color: colors.orangeYellow,
      }
    };

    const { countdown } = this.props;
    const playStatus = this.isPlaying();

    return (
      <div style={styles.root}>
        <Sound
          url="/static/audio/5s.mp3"
          autoLoad={true}
          playStatus={playStatus}
        />
        <div>{t('open_time')}</div>
        <div style={styles.time}>{countdown}</div>
      </div>
    );
  }
}

GameCounter.propTypes = {
  countdown: PropTypes.string.isRequired,
};

export default GameCounter;